<script>
  let enteredPassword;
  let passwordValidity;
  // let createdPasswords = [];

  function validatePassword() {
    if (enteredPassword.trim().length < 5) {
      passwordValidity = "too short";
    } else if (enteredPassword.trim().length > 10) {
      passwordValidity = "too long";
    } else passwordValidity = "done";
    return;
  }
</script>

<style>
  #form {
    width: 30rem;
    max-width: 100%;
  }
</style>

<div id="form">
  <div class="form-control"><input type="password" /></div>
</div>

<h1>Assignment</h1>

<p>Solve these tasks.</p>

<ol>
  <li>
    Add a enteredPassword input field and save the user input in a variable.
  </li>
  <li>
    Output "Too short" if the password is shorter than 5 characters and "Too
    long" if it's longer than 10.
  </li>
  <li>
    Output the password in a paragraph tag (below the input or below the
    warnings) if it's between these boundaries.
  </li>
  <li>
    Add a button and let the user add the passwords to an array (an array that
    grows with every password that was entered - not too long not too short).
  </li>
  <li>Output the array values (= passwords) in a unordered list (ul tag).</li>
  <li>Bonus: If a password is clicked, remove it from the list.</li>
</ol>

<div class="form">
  <div class="form-control">
    <label>Password</label>
    <input type="password" bind:value={enteredPassword} />
  </div>
</div>

<!-- password input field validation -->
{#if passwordValidity === 'too long'}
  <p>Password is too long</p>
{/if}

{#if passwordValidity === 'too short'}
  <p>Password is too short</p>
{/if}

{#if passwordValidity === 'too short' || passwordValidity == 'too long'}
  <p>Your password must be more than 5 and less than 10 characters</p>
{:else}
  <p>
    Success! Your password meets the criteria. This is your password:
    {password}
  </p>
{/if}

<button on:click={addPassword}>Add Password</button>

{#each createdPasswords as pass, i}
  <ContactCard createdPasswords={pass[i]} />
{:else}
  <ul>
    <li />
  </ul>
{/each}
